import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, ViewController, ModalController } from 'ionic-angular';
import { FormGroup, FormBuilder, Validators,FormGroupName, FormArray, FormControl } from '@angular/forms';
import { Http } from '@angular/http';
import { DataServiceProvider } from '../../providers/data-service/data-service';
import {HomePage} from '../home/home';
/**
 * Generated class for the AddBusinessPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
interface Response{
  status:number,
  message?:String,
  data?:any
}
@IonicPage()
@Component({
  selector: 'page-add-business',
  templateUrl: 'add-business.html',
})
export class AddBusinessPage {

  
  items: string[];
  contacts: FormArray;
  businessForm: FormGroup;


  constructor(public navCtrl: NavController,public http: Http,public modalCtrl:ModalController, public LoadingCtrl: LoadingController,  public fb:FormBuilder, public dataService:DataServiceProvider) {
  		

    /*   var  url='./assets/json/customer.json';
      this.http.get(url).subscribe(data => {
     this.items= this.customers = data.json();
            console.log(this.customers);
        });
 */
this.businessForm = fb.group({
  'bName':['',Validators.required],
  'owner':['',Validators.required],
  'address':['',Validators.required],
  'nearestTown':['',Validators.required],
  'district':['',Validators.required],
  contacts: this.fb.array([ this.createContact() ]),
  'email':['',Validators.required],
 // 'pno':['',Validators.required],
  'location':fb.group({
    'lat':['',Validators.required],
    'lon':['',Validators.required]
  })
});
    }
  ionViewDidLoad() {
    console.log('ionViewDidLoad AddBusinessPage');
   
  }
  createContact(): FormControl {
    return this.fb.control('');
  }

  addContact(): void {
    this.contacts = this.businessForm.get('contacts') as FormArray;
    this.contacts.push(this.createContact());
  }

  removeContact(idc:number):void{
    this.contacts.removeAt(idc);
  }

  selDistrict(){
    let districtModal = this.modalCtrl.create('SelectDistrictPage');
    districtModal.onDidDismiss(data => {
      if(data){
       this.businessForm.controls['district'].setValue(data);
      }
    });
    districtModal.present();
  }

  addBusiness(){
    this.dataService.addBusiness(this.businessForm.value).subscribe(data=>{
      let d:Response = <Response>data;
      console.log(d);
    });
  }
}



